.task-list {
    display: flex;
    justify-content: space-around;
    flex-basis: auto;
    flex-wrap: wrap;
    padding: 0 40px;
    border-bottom: 1px dotted blue;
    border-top: 1px dotted blue;
}

.task-list__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
